<template>
  <div id="app" class="flex-row align-center cover">
    <Person v-if="step === 1"
      @getCurrentResult="step = 2"></Person>

    <CurrentSummary v-else-if="step === 2"
      :users="users" 
      @addMorePerson="step = 1"
      @getFinalResult="step = 3"></CurrentSummary>

    <FinalResult v-else-if="step === 3"
      @restart="step = 1"></FinalResult>
  </div>
</template>

<script>
import Person from './components/Person.vue'
import CurrentSummary from './components/CurrentSummary.vue'
import FinalResult from './components/FinalResult.vue'

export default {
  name: 'app',
  components: {
    Person,
    CurrentSummary,
    FinalResult
  },
  data() {
    return {
      step: 1
    }
  },
  computed: {
    users() {
      return this.$store.state.users
    }
  },
}
</script>

